@-webkit-keyframes fade {
  0%   { opacity: 0; }
  11.11%   { opacity: 1; }
  33.33%  { opacity: 1; }
  44.44%  { opacity: 0; }
  100% { opacity: 0; }
}
@keyframes fade {
  0%   { opacity: 0; }
  11.11%   { opacity: 1; }
  33.33%  { opacity: 1; }
  44.44%  { opacity: 0; }
  100% { opacity: 0; }
}

.fadein { 
	position:relative; 
	width:100%;
	min-height: 156px;
	max-height:600px;
	margin-bottom: 365px;
	}
.fadein div { 
	position:absolute;
	width:100%; 
	min-height: 711px;
	max-height: 711px;
	left:0; 
	right:0; 
	opacity:0; 
	-webkit-animation-name: fade; 
	        animation-name: fade; 
	-webkit-animation-duration: 15s; 
	        animation-duration: 15s; 
	-webkit-animation-iteration-count: infinite; 
	        animation-iteration-count: infinite; 
	}
	.fadein img { 
		position:absolute;
		width:100%; 
		min-height: 711px;
		max-height: 711px;
		max-width: 1900px;
		left:0; 
		right:0; 
		}
.fadein div:nth-child(1) { -webkit-animation-delay: 0s; animation-delay: 0s;  }
.fadein div:nth-child(2) { -webkit-animation-delay: 5s; animation-delay: 5s;  }
.fadein div:nth-child(3) { -webkit-animation-delay: 10s; animation-delay: 10s;  }
